<template>
    
    <HeaderArea />
    <div class="comp">
        <div class="sel-box">
            <select v-model="selected" class="select-box">
                <option disabled value="">선택하세요.</option>
                <option v-for="option in options" :key="option" :value="option.value">{{ option.text }}</option>
            </select>
        </div>
        <p>선택된 값 : {{ selected }}</p>
    </div>
    
    <div class="comp">
        <div class="sel-box">
            <select v-model="selectedOption" @change="handleSelectChange">
                <option disabled value="">결제 방법을 선택해주세요</option>
                <option value="bank">계좌이체</option>
                <option value="card">체크/신용카드</option>
                <option value="naverpay">네이버페이</option>
            </select>
        </div>
    </div>
    
    <div class="checklist-wrap" v-if="checkboxes.length > 0">
      <div v-for="(checkbox, index) in checkboxes" :key="index" class="check-item">
        <input type="checkbox" :id="'checkbox' + index" :value="checkbox" v-model="selectedCheckboxes">
        <label :for="'checkbox' + index">{{ checkbox }}</label>
      </div>
    </div>

    <!-- 선택된 체크박스 확인 -->
    <div class="comp col2">
        <p>선택된 항목:</p>
        <ul class="checklist-item">
            <li v-for="(item, index) of selectedCheckboxes" :key="index">{{ item }}</li>
        </ul>
    </div>
    
</template>

<script setup>
import { ref } from 'vue';
import HeaderArea from '@/components/HeaderArea';

const selected = ref('')
const options = ref([
  { text: '하나', value: '1' },
  { text: '둘', value: '2' },
  { text: '셋', value: '3' }
])


const selectedOption = ref('')
const checkboxes = ref([])
const selectedCheckboxes = ref([])

const handleSelectChange = () => {
    switch(selectedOption.value) {
        case 'bank':
            checkboxes.value = ['국민', '신한', '농협']
            break;
        case 'card':
            checkboxes.value = ['samsung', 'hyundae', 'hana']
            break;
        case 'naverpay':
            checkboxes.value = ['네이버포인트', '네이버쇼핑', '네이버멤버쉽']
            break;
        default:
            checkboxes.value = []
    }

    selectedCheckboxes.value = []
}




</script>